.AnimationSelect-categories {
    display: grid;
    grid-template-columns: 1fr 2fr;
    max-width: 30rem;
}

.AnimationSelect-subCategory {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: center;
}

.AnimationSelect-subCategory button {
    margin: 0.2rem 0.5rem;
    flex: 1 1 auto;
}

.AnimationSelect-Standby {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: space-around;
}
